#skills-section {
    list-style: none;
    background: #3498DB;
    width: 100%; }

#skills {
    margin: 0 auto;
    text-align: center;
    background: -webkit-linear-gradient(#ff512f, #dd2476);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 35px 0 20px;
    width: 300px; }

#skills::before {
    content: "before";
    background: linear-gradient(#ff512f, #dd2476);
    font-size: 2px;
    display: block;
    color: black;
    width: 100%; }

#skills::after {
    content: "after";
    background: linear-gradient(#ff512f, #dd2476);
    font-size: 2px;
    display: block;
    color: black;
    width: 100%; }

.b-s { //bold-skills
    background: -webkit-linear-gradient(#ff512f, #dd2476);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.header {
    color: black;
    position: relative;
    margin-top: 0;
    margin-bottom: -20px;
    padding-top: 0; }

.description-2 {
    max-width: 900px;
    margin: 0 auto;
    margin-bottom: 0;
    font: {
        size: 18px; }
    text-align: justify; }

.description2-2 {
    position: relative;
