.list {
    width: 450px;
    display: inline-block;

    li {
        margin: 35px 0; }

    .bar {
        background: black;
        display: block;
        height: 35px;
        border: 1px solid rgba(0,0,0,0.3);
        border-radius: 4px;
        overflow: hidden;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 1px; }

    .bar span {
        height: 35px;
        float: left;
        background-image: linear-gradient(to right, rgba(255, 81, 47, 0.9), rgba(221, 36, 118, 0.7));
        border-radius: 4px;
        display: flex;
        padding: 0 15px;
        line-height: 35px;
        color: white; } }


