* {
    font-family: Montserrat; }

html, body {
    height: 100%; }

header {
    width: 100%;
    height: 100%;
    //background: black
    background: url(../images/night.jpg) center center;
    background-size: cover;

    nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 90px;
        padding: 10px 100px;
        box-sizing: border-box;
        transition: 0.5s;
        z-index: 3; }

    nav .logo {
        float: left; }

    nav .logo img {
        display: none;
        height: 80px;
        transition: 0.5s; }

    nav .logo img:hover {
        content: url("/assets/images/purple_logo.png"); }

    nav.black {
        background: rgba(0,0,0,0.7);
        transform: scale(1.1); }

    nav.black .logo img {
        display: block; }

    nav.black ul li a {
        display: block;
        font-weight: bold; }

    nav ul {
        float: right;
        margin: 0;
        padding: 0;
        display: flex; }

    nav ul li {
        list-style: none; }

    nav ul li a {
        line-height: 70px;
        color: white;
        padding: 5px 20px;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 300; }

    .title-wrap {
        position: absolute;
        bottom: 50%;
        width: 100%;
        height: 100px;
        text-align: center; }

    #title {
        display: inline-block;
        font: {
            size: 124px;
            weight: 100; }
        text-transform: uppercase;
        color: white;
        letter-spacing: 0.1em;
        margin: auto;
        opacity: 0.9; }

    .prompt {
        font-weight: 300;
        font-family: 'Montserrat', sans-serif;
        color: white;
        bottom: 40%;
        margin: 0 auto;
        text-align: center;
        padding-bottom: 15px;
        animation: animate 1.5s linear infinite; }

    .scroll-wrap {
        position: absolute;
        bottom: 20%;
        width: 100%;
        height: 80p; }

    .scroll-down {
        margin: 0 auto;
        width: 50px;
        height: 50px;
        transform: rotate(45deg);
        z-index: 1; }

    .scroll-down span {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        border-bottom: 1px solid #fff;
        border-right: 1px solid #fff;
        animation: animate 1.5s linear infinite;
        opacity: 0; }

    .scroll-down span:nth-child(1) {
        transform: translate(-15px, -15px); }

    .scroll-down span:nth-child(2) {
        transform: translate(0, 0); }

    .scroll-down span:nth-child(3) {
        transform: translate(15px, 15px); } }


#link0 {
    display: none; }

#link1.active, #link1:hover {
    background: -webkit-linear-gradient(#c33764, #1d2671);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

#link2.active, #link2:hover {
    background: -webkit-linear-gradient(#ff512f , #dd2476);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

#link3.active, #link3:hover {
    background: -webkit-linear-gradient(#7b4397, #dc2430);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

#link4.active, #link4:hover {
    background: -webkit-linear-gradient(#4568dc, #b06ab3);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

#particles-js {
    position: absolute;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 2; }

@keyframes animate {
    0% {
        top: -10px;
        left: -10px;
        opacity: 0; }
    50% {
        top: 0px;
        left: 0px;
        opacity: 1; }
    100% {
        top: 10px;
        left: 10px;
        opacity: 0; } }

@media screen and (max-width: 1300px) {
    header {
        nav .logo {
            display: none; }

        nav.black {
            display: none; }

        #title {
            font-size: 96px; } } }

@media screen and (max-width: 500px) {
    header {
        nav {
            display: none; } } }
